<template>
  <button @click="func_close_menu" v-if="!btnStatus" class="menu-btn" id="menu-close">
    <i class='bx bx-log-out-circle'></i>
  </button>

  <button @click="func_open_menu" v-if="btnStatus" class="menu-btn" id="menu-open">
    <i class='bx bx-menu'></i>
  </button>

</template>

<script>
import {useStore} from "vuex";
export default {
  name: "MenuBtnCop",
  components: {},
  props: {
    btnStatus: {
      type: Boolean,
      default: false,
    }
  },

  setup(){
    const store = useStore();
    const func_open_menu = () => {
      console.log('open');
      store.commit("changeMenuStatus", false);
    }
    const func_close_menu = () =>{
      console.log('close');
      store.commit("changeMenuStatus", true);
    }
    return{func_close_menu, func_open_menu}
  }
}
</script>

