import $ from 'jquery';
import {jwtDecode} from "jwt-decode";
import router from "@/router";
const  ModuleUser = {
    state: {
        // username: "",
        // uname: "",
        // id: "",
        // photo: "",
        // userphoto: "",
        // uid: "",
        // is_login: "",
        // access: "",
        // refresh: "",
        // followerCount: 0,
        username: JSON.parse(localStorage.getItem("user"))?.username || "",
        uname: JSON.parse(localStorage.getItem("user"))?.username || "",
        id: JSON.parse(localStorage.getItem("user"))?.id || "",
        photo: JSON.parse(localStorage.getItem("user"))?.photo || "",
        userphoto: JSON.parse(localStorage.getItem("user"))?.photo || "",
        uid: JSON.parse(localStorage.getItem("user"))?.uid || "",
        is_login: JSON.parse(localStorage.getItem("user"))?.is_login || "",
        access: JSON.parse(localStorage.getItem("user"))?.access || "",
        refresh: JSON.parse(localStorage.getItem("user"))?.refresh || "",
        intervalFunc: 0,
    },
    getters: {
    },
    mutations: {
        updateUser(state, user) {
            localStorage.setItem("user", JSON.stringify(user));
            document.cookie = "user=user; path=/";
            state.id = user.id;
            state.uid = user.uid;
            state.username = user.username;
            state.uname = user.username;
            state.photo = user.photo;
            state.userphoto = user.photo;
            state.followerCount = user.followerCount;
            state.access = user.access;
            state.refresh = user.refresh;
            state.is_login = user.is_login;
        },
        updateAccess(state, access) {
            if(access == null || !state.is_login) return false;
            let user = JSON.parse(localStorage.getItem("user"));
            user.access = access;
            localStorage.setItem("user", JSON.stringify(user));
            state.access = access;
        },
        logout(state) {
            window.clearInterval(state.intervalFunc);
            localStorage.clear();
            state.id = "";
            state.username = "";
            state.uname = "",
            state.followerCount = 0;
            state.access = "";
            state.refresh = "";
            state.is_login = false;
            state.intervalFunc = 0;
            let d = new Date();
            document.cookie = "user=user; expires=" + d.toUTCString() + "; path=/";
            // 返回到主界面
            router.push({name: 'home'});

        },
        setIntervalFunc(state, intervalFunc) {
            let user = JSON.parse(localStorage.getItem('user'));
            if(user !== null) {
                user.intervalFunc = intervalFunc;
                localStorage.setItem("user",  JSON.stringify(user));
            }
            state.intervalFunc = intervalFunc;
        }
    },
    actions: {
        login(context, data) {
            $.ajax({
                url: "https://app6866.acapp.acwing.com.cn/api/token/",
                type: "POST",
                data: {
                    username: data.username,
                    password: data.password,
                },
                success(resp) {
                    const {access, refresh} = resp;
                    const access_obj = jwtDecode(access);
                    context.dispatch("refresh_access", refresh).then();
                    $.ajax({
                        url: "https://app6866.acapp.acwing.com.cn/sakura/getinfo/",
                        type: "GET",
                        data: {
                            user_id: access_obj.user_id,
                        },
                        headers: {
                            'Authorization': "Bearer " + access,
                        },
                        success(resp) {
                            console.log(resp)
                            context.commit("updateUser", {
                                ...resp,
                                access: access,
                                refresh: refresh,
                                is_login: true,
                            });
                            data.success();
                        },
                    })

                },
                error() {
                    data.error();
                }
            });
        },
        refresh_access(context, refresh) {
            // 每4.5min刷新jwt
            let func = setInterval(() => {
                $.ajax({
                    url:'https://app6866.acapp.acwing.com.cn/api/token/refresh/',
                    type: "POST",
                    data: {
                        'refresh': refresh,
                    },
                    success(resp){
                        context.commit("updateAccess", resp.access);
                    },

                });
            }, 1000 * 60 * 3);
            context.commit("setIntervalFunc", func);
        },
        refresh_access_update(context, refresh) {
            $.ajax({
                url:'https://app6866.acapp.acwing.com.cn/api/token/refresh/',
                type: "POST",
                data: {
                    'refresh': refresh,
                },
                success(resp){
                    context.commit("updateAccess", resp.access);
                },

            });
        },
        act_update_user(context, data){
            const {user_info, api_flg} = data;
            console.log(user_info, api_flg);
            if(api_flg)
            {
                console.log(user_info.access);
                $.ajax({
                    url: "https://app6866.acapp.acwing.com.cn/sakura/getinfo/",
                    type: "PUT",
                    data: {
                        'uid': user_info.uid,
                        'photo': user_info.photo,
                        'id': user_info.id,
                    },
                    headers: {
                        'Authorization': "Bearer " + user_info.access,
                    },
                    success(resp) {
                        if(resp.result === 'success')
                        console.log('更新成功');
                        else
                            console.log(resp);
                        context.commit("updateUser",
                            user_info,
                        );
                    },
                });
            }
        },
    },

    modules: {
    }
};
export default ModuleUser;
