import { createRouter, createWebHashHistory, } from 'vue-router'
import HomeView from "@/views/HomeView";
import UserSetting from "@/views/UserSetting";
import MissionStatusView from "@/views/MissionStatusView";
import ClockInMissionView from "@/views/ClockInMissionView";
import NotFoundView from "@/views/NotFoundView";
const routes = [
  {
    path: '/sakura/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/sakura/setting',
    name: 'setting',
    component: UserSetting
  },
  {
    path: '/sakura/mission',
    name: 'mission',
    component: MissionStatusView
  },
  {
    path: '/sakura/clockin',
    name: 'clockin',
    component: ClockInMissionView,
  },
  {
    path: '/sakura/404',
    name: '404',
    component: NotFoundView,
  },
  {
    path: '/sakura/:catchAll(.*)',
    redirect: "/sakura/404/"
  }

]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})


export default router
