import { createStore } from 'vuex'
import ModuleUser from "@/store/user";
export default createStore({
  state: {
    menuStatus: true, // true: menu is open, false: menu is closed
  },
  getters: {
  },
  mutations: {
    changeMenuStatus(state, status) {
      console.log('enter');
      state.menuStatus = status;
      console.log(state.menuStatus);
    },
    judgeError(state, status){
      console.log(status);
      if(status === '' || status === 0 || status === 401 || status === 'error') {
        // 清空信息
        localStorage.clear();
        // 清空cookie
        let d = new Date();
        d.setTime(d.getTime() + -1);
        document.cookie = "user=user; expires=" + d.toUTCString() + "; path=/";
        // 强制下线
        this.commit('logout');
      }
    }

  },
  actions: {
  },
  modules: {
    user: ModuleUser,
  }
})
