<template>
  <div class="separator">
    <div class="info">
      <h3>任务记录</h3>
      <a @click="func_show_all" href="#">{{!allShowFlag ? "查看所有" : "缩略形式"}}</a>
    </div>
    <input type="date" :value="lv_today_date" @input="func_input_change">

  </div>
  <div class="planning">
    <div class = "item" v-for="k in filteredPlanLists" :key="k.i">
      <div class="left">
        <div class="icon">
          <i :class="k.icon"></i>
        </div>
        <div class="details">
          <h5>{{k.name}}</h5>
          <p>{{k.time}}</p>
        </div>
      </div>
      <i class = 'bx bx-dots-vertical-rounded' data-bs-toggle="dropdown" aria-expanded="false">
      </i>
      <ul class="dropdown-menu">
        <li><a data-bs-toggle="modal" data-bs-target="#staticBackdrop" class="dropdown-item dropdown-menu-delete" href="#">删除</a></li>
<!--        <li><a @click="editPlan(k)" class="dropdown-item disabled" href="#">修改()</a></li>-->
<!--        <li><a @click="viewDetails(k)" class="dropdown-item disabled" href="#">详情</a></li>-->
      </ul>
      <!-- Modal -->
      <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">

            <div class="modal-body">

              <div class="alert alert-danger" role="alert">
                <p>{{k.name}}</p>
                确定要删除该任务吗?
              </div>
            </div>
            <div class="modal-footer">
              <button @click="func_confirm(k)" type="button" class="btn btn-secondary" data-bs-dismiss="modal" > 确定 </button>
              <button @click="func_close" type="button" class="btn btn-primary" data-bs-dismiss="modal"> 取消 </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>


</template>

<script>
import {reactive, ref} from "vue";
import $ from 'jquery'
import {useStore} from "vuex";

export default {
  name: "PlanningCop",
  computed: {
    filteredPlanLists() {
      if (this.allShowFlag) {
        return this.allPlans.planLists;
      } else {
        return this.allPlans.planLists.filter(k => k.id <= 4);
      }
    }
  },
  setup(){
    const allShowFlag = ref(false);

    const store = useStore();
    const cur_date = ref();
    const allPlans = reactive({
      planLists: [
      ]
    });
    const getTodayDate = () => {
      const today = new Date();
      const year = today.getFullYear();
      const month = (today.getMonth() + 1).toString().padStart(2, '0'); // 加 1 是因为月份从 0 开始计数，padStart 补零
      const day = today.getDate().toString().padStart(2, '0');
      let lv_today_str = `${year}-${month}-${day}`;
      return lv_today_str;
    }
    const lv_today_date = ref();
    lv_today_date.value = getTodayDate();
    console.log(lv_today_date.value);
    const func_is_today = (date) => {
      const today = new Date();
      return date.toDateString() === today.toDateString();
    }
    const parseTaskString = (taskString, id) => {
      const timeMatch = taskString.match(/(\d{2}:\d{2}:\d{2}) ~ (\d{2}:\d{2}:\d{2})/);
      const time = timeMatch ? `${timeMatch[1]} ~ ${timeMatch[2]}` : '';

      // 提取学习任务名称
      const nameMatch = taskString.match(/学习任务: ([\u4E00-\u9FFF\w]+)/);
      const name = nameMatch ? nameMatch[1] : 'Unknown';
      console.log(time, name);
      // 构建结果对象
      const result = {
        name: name,
        time: time,
        icon: 'bx bx-time-five',
        id: id
      };
      return result;
    };
    const func_show_today_task= ()=>{
      if(store.state.user.uid === "")
        return;
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/getjson/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          'uid': store.state.user.uid,
        },
        success(resp){
          if(resp.result === 'success')
          {
            // TODO
            // 渲染对应的画面
            // console.log(resp.json)
            let study_task_info = resp['json']['study_tasks_info'];
            let study_task_list = study_task_info['today_task_list']

            allPlans.planLists = [];
            for(let i = 0; i < study_task_list.length; i++)
            {
              let res = parseTaskString(study_task_list[i], i + 1);
              allPlans.planLists.push(res);
            }
          }
          else
          {
            console.log(resp);
          }
        }
      });
    };

    const func_show_other_task = ()=>{
      if(store.state.user.uid === "")
        return;
      $.ajax({
        url: "https://app6866.acapp.acwing.com.cn/sakura/optask/",
        type: "GET",
        headers: {
          'Authorization': "Bearer " + store.state.user.access,
        },
        data: {
          uid: store.state.user.uid, // 需要先绑定 uid
        },
        success(resp){
          if(resp.result == 'success')
          {
            // TODO
            // 渲染对应的画面
            let info = resp['json']['study_status'];
            let currentDate = new Date(cur_date.value);
            currentDate.setDate(currentDate.getDate() + 1);
// 分离出年、月、日
            let year = currentDate.getFullYear();
            let month = String(currentDate.getMonth() + 1).padStart(2, '0'); // 月份从0开始，所以需要加1
            let day = String(currentDate.getDate()).padStart(2, '0');
            let formattedDate = `${year}-${month}-${day}`;
            // console.log(info[year][month][formattedDate]);
            let task_list = []
            if(formattedDate in info[year][parseInt(month)])
            {
              task_list = info[year][parseInt(month)][formattedDate]['study_mission_dict'];
            }
            let study_task_list = task_list;
            allPlans.planLists = [];
            for(let i = 0; i < study_task_list.length; i++)
            {
              console.log(study_task_list[i]);
              let res = parseTaskString(study_task_list[i], i + 1);
              allPlans.planLists.push(res);
            }
          }
          else
          {
            console.log(resp);
          }
        }
      });
    };



    const func_input_change = (event) => {
      cur_date.value = event.target.value;
      lv_today_date.value = cur_date.value;
      if(func_is_today(new Date(cur_date.value)))
      {
        func_show_today_task();
      }
      else
      {
        func_show_other_task();
      }
    };

    const func_ver_click = (plan) =>{
      plan.showDropdown = !plan.showDropdown;
    };
    const deletePlan = (plan) => {
      let id = plan.id
      // 删除 id
      allPlans.planLists = allPlans.planLists.filter((plan) => plan.id !== id);
      let idx = 0
      for(let k in allPlans.planLists) // update id
      {
        allPlans.planLists[k].id = idx + 1;
        idx ++ ;
      }
    };

    const editPlan = (plan) => {
      console.log(plan);
    };
    const func_close = () =>{

    };
    const func_confirm = (k) =>{
      deletePlan(k)
    };
    const viewDetails = (plan) => {
      console.log(plan);
    };
    const func_show_all = () => {
      allShowFlag.value = !allShowFlag.value;
    };

    return {allPlans, func_ver_click, deletePlan, editPlan, viewDetails, func_close, func_confirm, func_show_all, allShowFlag, cur_date, func_show_other_task, func_show_today_task,
      func_input_change, lv_today_date,
            }
  }
}
</script>

<style scoped>
.dropdown-menu{
  background-color: #F7F7F7;
  border-radius: 10%;
}
.dropdown-menu:hover{
}
.dropdown-menu-delete{
  color:red
}
.planning {
  transition: height 3s ease-in-out;
  overflow: hidden;
  max-height: 222px;
  overflow-y: auto;
  animation: move 1.2s
}


 .separator input{
  border: none;
  color: #369FFF;
  font-weight: bold;
  outline: none;
  width: 120px;
}

input[type="date"]::-webkit-calendar-picker-indicator{
  opacity: 0.5;
  font-size: 18px;
  cursor: pointer;
}

</style>
