<template>
  <head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.2/css/all.min.css">
    <meta name="referrer" content="no-referrer">
    <title>sakura小姐</title>
  </head>


    <div class="cont">
      <LeftSectionCop />
      <router-view/>
      <RightSectionCop></RightSectionCop>
    </div>
<!--  <NotFoundView></NotFoundView>-->
<!--  <LoginView v-else></LoginView>-->
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min';
import '@/assets/style.css';
import LeftSectionCop from "@/components/LeftSectionCop";
import RightSectionCop from "@/components/RightSectionCop";
// import NotFoundView from "@/views/NotFoundView";
// import LoginView from "@/views/LoginView";
import {useStore} from "vuex";

export default{
  name: "App",
  computed: {
    cur_page(){
      return this.$route.name;
    },
    need_show(){
      return this.$route.name != '404';
    }
  },
  components:{
    LeftSectionCop,
    RightSectionCop,
    // NotFoundView,
    // LoginView,
  },
  mounted() {
    const store = useStore();
    // 关闭浏览器清除localStorage
    // 方法一：通过cookie的生命周期控制localStorage
    // 查看cookie
    let cookieName = "user=";
    let ck = document.cookie.split(';');    //把cookie分割成组
    let isExist = false;
    for (let i = 0; i < ck.length; i++) {
      let t = ck[i]; //取得字符串
      while (t.charAt(0) == ' ') {        //判断一下字符串有没有前导空格
        t = t.substring(1, t.length);   //有的话，从第二位开始取
      }
      if (t.indexOf(cookieName) == 0) { //如果不含有我们要的name
        isExist = true;
        break;
      }
    }
    // 若cookie不存在就删除localStorage
    if(!isExist) {
      localStorage.clear();
    }

    // 方法二：通过在关闭浏览器前清除localStorage
    /**
     * 刷新页面 就算是简单helloworld都不小于5毫秒, 获取时间差来判断刷新与关闭页面
     */
    /*
    let start_stamp = 0, // 开始时间
    differ_time = 0; // 时间差
    window.onunload = () => {
        differ_time = new Date().getTime() - start_stamp;
        if (differ_time <= 5) localStorage.clear();
    };
    window.onbeforeunload = () => {
        start_stamp = new Date().getTime();
    };
    */

    // 刷新
    window.onload = () => {
      // 刷新后定时函数则失效, 因此我们重新调用，因为可能多次进入网页，所以需要清空定时函数
      window.clearInterval(store.state.user.intervalFunc);
      let user = JSON.parse(localStorage.getItem("user"));

      // 只有登陆了才需要更新token
      if(user !== null) store.dispatch("refresh_access", user.refresh);
    }
  },
}
</script>

