<template>

  <div class="sidebar">
    <div v-for="k in allItems.itemLists" :key="k.id">
      <router-link :to="k.urlName" @click="funcSelectItem(k.id)" class="item" :id=" k.urlName === cur_page ? 'active' : ''">
        <i :class="k.icon"></i>
        <a> {{k.name}}</a>
      </router-link>
    </div>

  </div>

</template>

<script>
import {reactive, ref} from "vue";
export default {
  name: "SideBarCop",
  computed: {
    cur_page(){
      return this.$route.name;
    }
  },
  setup(){
    const curSelectItem = ref(1);
    const allItems = reactive({
      itemLists: [
        {id: 1, name: "主页", icon: "bx bx-home-alt-2", urlName: 'home'},
        {id: 2, name: "任务", icon: "bx bx-grid-alt", urlName: 'mission'},
        {id: 3, name: "打卡", icon: "bx bx-folder", urlName: 'clockin'},
        // {id: 4, name: "信息", icon: "bx bx-message-square-dots", urlName: 'message'},
        // {id: 5, name: "设置", icon: "bx bx-cog", urlName: 'setting'},
      ]
    });
    const funcSelectItem = (id) => {
      if(id == curSelectItem.value)
          return;
      curSelectItem.value = id;
    };
    return {allItems, curSelectItem, funcSelectItem}
  }
}
</script>

